import { graphql } from "gatsby"
import React from "react";
import {MainSectionWrapper} from "../layout";
import CommonHead from "../layout/components/Head";
import PetitsAnimaux from "../layout/components/PetitsAnimaux";

const PageTemplate = (props) => {
  return (
    <MainSectionWrapper>
      <PetitsAnimaux {...props} />
    </MainSectionWrapper>
  )
};

export function Head(props) {
  return <CommonHead {...props} />;
}

export const query = graphql`
  query PetitsAnimaux {
    allGoogleDocs(filter: {directory: {eq: "petitsanimaux"}}) {
      edges {
        node {
          id
          name
          url
          price
          productType
          childMarkdownRemark {
            htmlAst
          }
        }
      }
    }
  }
`;

export default PageTemplate;