import React, {useMemo} from "react";
import renderAst from "../../renderAst";
import {H2, H3, H4} from "../Header";
import Link from "../Link";
import CarouselWrapper from "../Carousel";

import "./styles.scss";

const productTypeMapping = {
  "medium_crochet": {
    title: "Life size crochets"
  },
  "small_crochet": {
    title: "Crochet Keychains"
  },
  "large_plushies": {
    title: "Plushies"
  },
}

const PetitsAnimaux = (props) => {
  const {data} = props;
  const products = useMemo(
    () => {
      const categories = {};

      data?.allGoogleDocs?.edges.forEach(({node}) => {
        const {productType} = node;
        if (productTypeMapping[productType]) {
          if (!categories[productType]) {
            categories[productType] = [];
          }
          categories[productType].push(node);
        }
      });

      return Object.keys(categories)
        .filter(key => categories[key].length > 0)
        .map(key => {
          return {
            type: key,
            products: categories[key],
            metadata: productTypeMapping[key],
          };
        });
    }, 
    [data]
  );

  if (!props.pageContext || products.length === 0) {
    return null;
  }

  return (
    <>
      {products.map((productMetadata) => {
        const {metadata, products} = productMetadata;
        return (
          <div key={productMetadata.type}>
            <H2>{metadata.title}</H2>

            <CarouselWrapper>
              {products.map((node) => {
                const {childMarkdownRemark} = node;
                return (
                  <Link key={node.id} className="PetitsAnimauxCardLink" href={node.url} useTheming={false}>
                    <div className="PetitsAnimauxCard">
                      <div className="PetitsAnimauxCardImage">
                        {renderAst(childMarkdownRemark.htmlAst)}
                      </div>
                      <H3>{node.name}</H3>
                      <H4>{node.price}</H4>
                    </div>
                  </Link>
                );
              })}
            </CarouselWrapper>

          </div>
        );
      })}
    </>
  )
};

export default PetitsAnimaux;